@font-face {
  font-family: "Bodoni";
  src: url("../../../fonts/Bodoni.woff2") format("woff2"),
    url("../../../fonts/Bodoni.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

.sectionName {
  background-color: #eeeeee;
  display: flex;
  height: 100vh;
  justify-content: center;
}

.container {
  display: flex;
  width: 100vw;
  height: 100vh;
  max-width: 2440px;
  align-items: center;
  justify-content: center;
  /* flex-wrap: wrap; */
}
.container__mainPicture {
  margin-left: -13%;
  margin-top: -2.5%;
  display: flex;
  height: 110vh;
  width: 40vw;
  transform: scale(0.75);
  background-image: url("../../../imges/MainPhoto/AboutPhoto.png");
  background-repeat: no-repeat;
  background-size: cover;
}
.container__aboutDescription {
  margin-left: 3vw;
  word-wrap: break-word;
  display: flex;
  /* border: 1px solid red; */
  width: 35vw;
  height: 90vh;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  box-sizing: border-box;
}
.bodySub5part1 {
  position: absolute;
  flex-wrap: wrap;
  display: flex;
  left: 4%;
  top: 305%;
  color: black;
  font-weight: bold;
  transform: translateX(-420%);
  opacity: 0;
  font-size: 2rem;
  transition: all 0.5s ease 0s;
}
.bodySub5part1._active {
  transform: translate(0px, 0px);
  opacity: 1;
  transition: all 0.4s ease 0.2s;
}
.bodySub5part2 {
  display: flex;
  width: 50%;
  height: 40%;
  font-weight: bold;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.description {
  display: flex;
  flex-direction: column;
  margin-top: 9%;
  font-family: "Bodoni", arial;
  text-shadow: 8px 8px 8px #aaa;
  font-size: 7vh;
  text-align: left;
}

.container__past {
  word-wrap: break-word;
  line-height: 6vh;
  letter-spacing: 1px;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  width: 100%;
  height: 50%;
  /* margin-left: ; */
}
.description__past__points {
  margin-top: 1%;
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
}

.description__text {
  list-style-type: none;
  margin-left: 0;
  padding-left: 0;
  display: flex;
  flex-direction: column;
  word-wrap: break-word;
  font-size: 3vh;
  text-shadow: 4px 4px 4px #aaa;
}

@media screen and (max-width: 1440px) {
  .container__mainPicture {
    margin-left: -20%;
    width: 58vw;
    height: 120vh;
    transform: scale(0.6);
  }
  .container__aboutDescription {
    width: 45vw;
  }
  .FooterPart7 {
    display: none;
  }
  .FooterPart3 {
    display: none;
  }
}

@media screen and (max-width: 1080px) {
  .container__aboutDescription {
    width: 60vw;
    margin-top: 7%;
    margin-left: -12%;
    word-break: normal;
    white-space: nowrap;
    transform: scale(0.85);
    /* width: auto; */
  }
  .container__mainPicture {
    width: 70vw;
    transform: scale(0.65);
  }
  .FooterPart3 {
    display: none;
  }
}
@media screen and (max-width: 900px) {
  .container__aboutDescription {
    width: 50vw;
    margin-top: 7%;
    margin-left: -13%;
    word-break: normal;
    white-space: nowrap;
    transform: scale(0.73);
    /* width: auto; */
  }
  .container__mainPicture {
    width: 65vw;
    height: 77vh;
    transform: scale(auto);
    transform: scale(0.75);
  }
}
@media screen and (max-width: 780px) {
  .container__aboutDescription {
    width: 50vw;
    margin-top: 7%;
    margin-left: -17%;
    word-break: normal;
    white-space: nowrap;
    transform: scale(0.6);
    /* width: auto; */
  }
  .container__mainPicture {
    margin-left: -27%;
    width: 62vw;
    height: 70vh;
    transform: scale(0.65);
  }
}
@media screen and (max-width: 580px) {
  .container__aboutDescription {
    width: 100vw;
    margin-top: 7%;
    margin-left: -5%;
    word-break: normal;
    white-space: nowrap;
    transform: scale(0.8);
    /* width: auto; */
  }
  .container__mainPicture {
    display: none;
  }
}

@media screen and (max-width: 480px) {
  .container__aboutDescription {
    word-break: break-all;
    transform: scale(0.6);
    margin-left: -15%;
  }
}
@media screen and (max-width: 380px) {
  .container__aboutDescription {
    margin-left: -20%;
  }
}
